import React, { Component, createContext } from 'react';
import Axios from "axios";

export const PageContext = createContext({ state: null });

class PageProvider extends Component {
	state = {
		// pages: null,
		pgObj: null,
		frontPageId: null,
	};

	wpInfo = {
		baseUrl: '/wp-json/wp/v2/',
		perPage: '?per_page=10',
		wpFetchHeaders: {
			headers: {
				'Access-Control-Allow-Origin': '*',
				'Access-Control-Expose-Headers': 'x-wp-total'
			},
		},
	};

	unsubscribeFromPages = null;

	getNumPages = async () => {
		const { baseUrl, perPage, wpFetchHeaders } = this.wpInfo;
		const url = `${baseUrl}pages${perPage}`;

		const { headers } = await Axios(url, wpFetchHeaders);
		return headers['x-wp-totalpages'];
	}

	fetchPages = async (numPages) => {
		const { baseUrl, perPage, wpFetchHeaders } = this.wpInfo;
		const { frontPageId } = this.state;
		const pagesArr = [];

		for (let pg = 1; pg <= numPages; pg += 1) {
			const url = `${baseUrl}pages${perPage}&page=${pg}`;
			const page = Axios.get(url, wpFetchHeaders);
			pagesArr.push(page);
		}

		await Axios.all(pagesArr)
			.then((response) => {
				const pageData = response.map((res) => res.data).flat();
				// this.setState({ pages: pageData });
				return pageData;
			})
			.then((pageArr) => {
				let pgObj = {};

				pageArr.forEach((page) => {
					if (page.id === frontPageId) {
						pgObj.index = page;
					} else {
						pgObj[page.slug] = page;
					}
				});
				this.setState({ pgObj });
			})
			.catch((err) => console.log('error fetching pages: ', err)
		);

		return true;
	}

	getFrontPage = async () => {
		const { baseUrl, wpFetchHeaders } = this.wpInfo;
    const url = `${baseUrl}frontpage`;
    
    await Axios(url, wpFetchHeaders)
    	.then(({ data }) => this.setState({ frontPageId: data.id }))
    	.catch((err) => console.log('error fetching frontpage id: ', err)
		);

    return true;
  }

	componentDidMount = () => {
		this.unsubscribeFromPages = this.getFrontPage()
			.then(() => this.getNumPages())
			.then((numPages) => this.fetchPages(numPages))
			.catch((err) => console.error('err fetching page data', err));
	}

	componentWillUnmount = () => {
		this.unsubscribeFromPages();
	}

	render() {
		// const { data } = this.state;
		const { children } = this.props;
		return (
			<PageContext.Provider value={ this.state }>{ children }</PageContext.Provider>
		);
	}
}

export default PageProvider;